@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;

700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "DM Sans", sans-serif;
  text-decoration: none;
  list-style: none;
  outline: none;
}

html {
  scroll-behavior: smooth;
}

::selection {
  background-color: #118DA8;
  color: #ffffff;
}

body {
  background-color: #191919;
  color: #ffffff;
}

body::-webkit-scrollbar {
  background-color: rgb(34, 34, 34);
  width: 11px;
}

body::-webkit-scrollbar-thumb {
  background-color: #118DA8;
}

body::-webkit-scrollbar-thumb:hover {
  background-color: #00d0ff;
}

.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 20px;
}

.title {
  font-size: 64px;
  font-weight: 400;
  letter-spacing: 2.88px;
}

.header__section {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 80px;
  z-index: 99999;
  transition: 0.2s ease;
}

.header__section.nav-changed {
  background-color: #191919;
  height: 70px;
}

.header__section.nav-changed .header__navbar {
  height: 70px;
}

.header__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  transition: 0.2s ease;
}

.header__navbar .nav-links ul {
  display: flex;
}

.header__navbar .nav-links ul li a {
  color: #ffffff;
  padding: 0 20px;
  font-size: 17px;
  font-weight: 400;
  transition: 0.2s ease;
}

.header__navbar .nav-links ul li a:hover {
  color: #118DA8;
}

.header__navbar .nav-links ul li a.link-active {
  color: #118DA8;
}

.header__navbar .burger .bar {
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  margin: 6px 0;
}

.header__navbar .burger {
  cursor: pointer;
  display: none;
}

.header__navbar .navside {
  position: absolute;
  top: 0;
  right: 0;
  width: 350px;
  height: 100vh;
  padding: 0 20px;
  background-color: rgba(0, 0, 0, 0.959);
  transform: translateX(120%);
  transition: 0.3s ease;
}

.header__navbar .navside.nav-active {
  transform: translateX(0);
}

.header__navbar .navside .close__btn {
  position: absolute;
  top: 20px;
  left: 90%;
  transform: translateX(-50%);
  cursor: pointer;
  width: 40px;
  height: 40px;
}

.header__navbar .navside .close__btn .bar {
  width: 30px;
  height: 4px;
  background-color: #ffffff;
  transform: rotate(-45deg) translate(-10px, 15px);
}

.header__navbar .navside .close__btn .bar:nth-child(2) {
  transform: rotate(45deg) translate(13px, 7px);
}

.header__navbar .navside ul {
  margin-top: 90px;
}

.header__navbar .navside ul li a {
  display: block;
  color: #ffffff;
  padding: 10px 0;
  font-size: 17px;
  font-weight: 400;
  transition: 0.2s ease;
}

.header__navbar .navside ul li a:hover {
  color: #118DA8;
  padding-left: 5px;
}

.header__navbar .navside ul li a.link-active {
  color: #118DA8;
}

.header__navbar .navside .nav-icons {
  display: flex;
  column-gap: 13px;
}

.header__navbar .navside .nav-icons li {
  transition: 0.2s ease;
}

.header__navbar .navside .nav-icons li a {
  padding: 5px;
}

.header__navbar .navside .nav-icons li:hover {
  margin-top: -5px;
}

.home__section {
  padding-top: 120px;
}

.home__section .home__items {
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 20px;
}

.home__section .home__items .home__image__item {
  max-width: 650px;
}

.home__section .home__items .home__image__item .home_image {
  width: 100%;
}

.home__section .home__items .home__text__item {
  position: relative;
  max-width: 526px;
}

.home__section .home__items .home__text__item .home__title {
  font-size: 70px;
  line-height: 88px;
  font-weight: 400;
  letter-spacing: 3.2px;
}

.home__section .home__items .home__text__item .line_text {
  font-size: 75px;
  letter-spacing: 3.2px;
}

.home__section .home__items .home__text__item .line_text span {
  font-size: 80px;
  font-weight: 700;
}

.home__section .home__items .home__text__item .social_icons {
  margin-top: 90px;
}

.home__section .home__items .home__text__item .social_icons .social__title {
  font-size: 15px;
  font-weight: 400;
}

.home__section .home__items .home__text__item .social_icons ul {
  display: flex;
  column-gap: 17px;
}

.home__section .home__items .home__text__item .social_icons ul li {
  margin-top: 16px;
  transition: 0.2s ease;
}

.home__section .home__items .home__text__item .social_icons ul li:hover {
  margin-top: 12px;
}

.toUp_btn {
  position: fixed;
  top: 140%;
  right: 0;
  transform: translate(-50%, -50%);
  background-color: #118DA8;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  transition: 0.2s ease;
  z-index: 8888;
}

.toUp_btn a {
  font-size: 50px;
  color: #ffffff;
}

.toUp_btn.show-btn {
  position: fixed;
  top: 90%;
}

.toUp_btn:hover {
  background-color: #005669;
}

.episodes__section {
  padding-top: 93px;
}

.episodes__title_item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.episodes__title_item .episodes_btn {
  padding: 12px 20px;
  border-radius: 6px;
  background-color: #118DA8;
  border: none;
  font-size: 17px;
  color: #ffffff;
  cursor: pointer;
  transition: 0.2s ease;
}

.episodes__title_item .episodes_btn:hover {
  background-color: #005669;
}

.episodes__item {
  display: flex;
  justify-content: space-between;
  column-gap: 15px;
  background-color: #000000;
  border-radius: 32px;
  padding: 48px;
  margin-top: 60px;
}

.episodes__item .episodes__text_item {
  max-width: 757px;
}

.episodes__item .episodes__text_item .tips {
  font-size: 18px;
}

.episodes__item .episodes__text_item .episode_item_title {
  color: #118DA8;
  font-size: 17px;
  margin-top: 48px;
}

.episodes__item .episodes__text_item .episode__block_title {
  font-size: 64px;
  margin-top: 18px;
  line-height: 70.4px;
  letter-spacing: 2.56px;
}

.episodes__item .episodes__text_item .episode__block_text {
  font-size: 22px;
  line-height: 33px;
  font-weight: 400;
  opacity: 0.7;
  margin-top: 28px;
}

.episodes__item .episodes__text_item .episode__block_btn {
  padding: 12px 20px;
  border-radius: 6px;
  background-color: #118DA8;
  border: none;
  font-size: 17px;
  color: #ffffff;
  cursor: pointer;
  margin-top: 18px;
  transition: 0.2s ease;
}

.episodes__item .episodes__text_item .episode__block_btn:hover {
  background-color: #005669;
}

.episodes__item .image__item {
  max-width: 390px;
  min-width: 150px;
}

.episodes__item .image__item .episode_img {
  width: 100%;
}

.about__section {
  padding-top: 130px;
}

.about__items {
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 20px;
}

.about__items .about__text_item {
  max-width: 556px;
}

.about__items .about__text_item .arrow_right {
  background-color: #118DA8;
  width: 112px;
  height: 112px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.2s ease;
}

.about__items .about__text_item .arrow_right:hover {
  background-color: #005669;
}

.about__items .about__text_item .arrow_right .about_arrow {
  font-size: 70px;
}

.about__items .about__text_item .about__small_title {
  margin-top: 20px;
  color: #118DA8;
  font-size: 18px;
}

.about__items .about__text_item .title {
  margin-top: 20px;
}

.about__items .about__text_item .about_text {
  margin-top: 28px;
  font-size: 22px;
  opacity: 0.7;
  font-weight: 400;
  line-height: 33px;
}

.about__items .about__image_item {
  max-width: 650px;
}

.about__items .about__image_item .about__image {
  width: 100%;
}

.contact__section {
  padding-top: 160px;
}

.contact__items {
  background-color: #000000;
  border-radius: 32px;
  padding: 60px;
  display: flex;
  justify-content: space-between;
}

.contact__items .contact__text_item {
  max-width: 400px;
}

.contact__items .contact__text_item .contact__small_title {
  color: #118DA8;
  font-size: 18px;
}

.contact__items .contact__text_item .title {
  margin-top: 15px;
}

.contact__items .contact__inputs_item {
  display: block;
  max-width: 582px;
}

.contact__items .contact__inputs_item .contact_input {
  width: 100%;
  height: 82px;
  background-color: #191919;
  padding: 19px 26px;
  font-size: 24px;
  color: #ffffff;
  border: none;
  border-radius: 6px;
}

.contact__items .contact__inputs_item .contact_input::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.contact__items .contact__inputs_item .contact_input:focus {
  border: 1px solid #118DA8;
}

.contact__items .contact__inputs_item .contact_input:nth-child(2) {
  margin-top: 20px;
}

.contact__items .contact__inputs_item .contact_btn {
  padding: 15px 20px;
  margin-top: 20px;
  cursor: pointer;
  border: none;
  border-radius: 6px;
  background-color: #118DA8;
  color: #ffffff;
  font-size: 15px;
  transition: 0.2s ease;
}

.contact__items .contact__inputs_item .contact_btn:hover {
  background-color: #005669;
}

.members {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  grid-gap: 20px;
  margin-top: 150px;
}

.members .member__block {
  width: 100%;
  background-color: #000000;
  padding: 50px;
  border-radius: 32px;
}

.members .member_star {
  color: #FCFF51;
  font-size: 17px;
}

.members .member__text {
  margin-top: 20px;
  font-size: 36px;
  max-width: 313px;
  line-height: 40px;
}

.members .member__name {
  margin-top: 20px;
  font-size: 24px;
  color: rgba(255, 255, 255, 0.7);
}

.footer__section {
  margin-top: 180px;
}

.footer__items {
  display: flex;
  justify-content: space-between;
  column-gap: 20px;
}

.footer__items .footer_item .footer_logo {
  max-width: 200px;
  width: 100%;
}

.footer__items .footer_item .footer__social_icons {
  margin-top: 26px;
}

.footer__items .footer_item .footer__social_icons ul {
  display: flex;
  column-gap: 13px;
}

.footer__items .footer_item .footer__social_icons ul li {
  transition: 0.2s ease;
}

.footer__items .footer_item .footer__social_icons ul li:hover {
  margin-top: -5px;
}

.footer__items .footer__item_links {
  max-width: 200px;
  width: 100%;
}

.footer__items .footer__item_links ul li {
  margin-top: 10px;
}

.footer__items .footer__item_links ul li:nth-child(1) {
  margin-top: 0;
}

.footer__items .footer__item_links ul li a {
  display: block;
  font-size: 17px;
  color: #ffffff;
  transition: 0.2s ease;
}

.footer__items .footer__item_links ul li a:hover {
  padding-left: 5px;
  color: #118DA8;
}

.create {
  text-align: center;
  padding: 60px 0;
}

.create a {
  color: #ffffff;
  transition: 0.2s ease;
  font-size: 17px;
  letter-spacing: 1px;
}

.create a:hover {
  color: #118DA8;
}

.create span {
  font-weight: 700;
}

@media screen and (min-width: 621px) {
  .header__navbar .navside {
    display: none;
  }
}

@media screen and (max-width: 1190px) {
  .episodes__item .episodes__text_item .episode__block_title {
    font-size: 54px;
    line-height: 60px;
  }

  .episodes__item .episodes__text_item .episode__block_text {
    font-size: 18px;
    line-height: 27px;
  }
}

@media screen and (max-width: 1124px) {
  .members .member__block {
    padding: 40px;
  }

  .members .member__text {
    font-size: 30px;
    line-height: 30px;
  }
}

@media screen and (max-width: 1090px) {
  .episodes__item .episodes__text_item .episode__block_title {
    font-size: 44px;
    line-height: 50px;
    letter-spacing: 1.56px;
  }

  .members {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 980px) {
  .episodes__item {
    display: flex;
    flex-direction: column;
    row-gap: 30px;
  }
}

@media screen and (max-width: 950px) {
  .home__section .home__items .home__text__item .home__title {
    font-size: 50px;
    line-height: 60px;
  }

  .home__section .home__items .home__text__item .line_text {
    font-size: 50px;
  }

  .home__section .home__items .home__text__item .line_text span {
    font-size: 50px;
  }

  .about__items .about__text_item .about_text {
    font-size: 18px;
    line-height: 25px;
  }

  .footer__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 50px;
  }
}

@media screen and (max-width: 820px) {
  .home__section .home__items {
    display: flex;
    flex-direction: column-reverse;
    row-gap: 50px;
  }

  .home__section .home__items .home__text__item .home__title {
    font-size: 70px;
    line-height: 88px;
  }

  .home__section .home__items .home__text__item .line_text {
    font-size: 75px;
  }

  .home__section .home__items .home__text__item .line_text span {
    font-size: 80px;
  }

  .home__section .home__items .home__text__item .social_icons {
    margin-top: 50px;
  }

  .about__items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 30px;
  }

  .contact__items {
    display: flex;
    flex-direction: column;
    row-gap: 30px;
  }
}

@media screen and (max-width: 768px) {
  .title {
    font-size: 54px;
    letter-spacing: 2px;
  }

  .contact__items .contact__inputs_item .contact_input {
    height: 75px;
  }
}

@media screen and (max-width: 620px) {
  .title {
    font-size: 40px;
    letter-spacing: 2px;
  }

  .header__navbar .nav-links {
    display: none;
  }

  .header__navbar .burger {
    display: block;
  }

  .members {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
}

@media screen and (max-width: 550px) {
  .contact__items {
    padding: 40px;
  }

  .contact__items .contact__inputs_item .contact_input {
    height: 60px;
  }
}

@media screen and (max-width: 540px) {
  .home__section .home__items .home__text__item .home__title {
    font-size: 50px;
    line-height: 60px;
  }

  .home__section .home__items .home__text__item .line_text {
    font-size: 50px;
  }

  .home__section .home__items .home__text__item .line_text span {
    font-size: 50px;
  }

  .episodes__item .episodes__text_item .episode__block_title {
    font-size: 32px;
    line-height: 40px;
    letter-spacing: 1px;
  }

  .episodes__item .episodes__text_item .episode__block_text {
    font-size: 16px;
    line-height: 24px;
  }

  .footer__items {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
}

@media screen and (max-width: 420px) {
  .title {
    font-size: 30px;
  }

  .home__section .home__items .home__text__item .home__title {
    font-size: 40px;
    line-height: 45px;
  }

  .episodes__title_item {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
  }

  .contact__items {
    padding: 30px;
  }

  .contact__items .contact__inputs_item .contact_input {
    height: 50px;
  }

  .members .member__text {
    font-size: 25px;
    line-height: 27px;
  }
}

@media screen and (max-width: 350px) {
  .home__section .home__items .home__text__item .home__title {
    font-size: 30px;
    line-height: 40px;
  }

  .home__section .home__items .home__text__item .line_text {
    font-size: 40px;
  }

  .home__section .home__items .home__text__item .line_text span {
    font-size: 40px;
  }

  .home__section .home__items .home__text__item .social_icons ul li img {
    width: 25px;
  }

  .members .member__text {
    font-size: 18px;
    line-height: 24px;
  }

  .footer__items .footer_item .footer__social_icons ul li img {
    width: 25px;
  }
}